import React, { Component } from 'react'
import { getCategories, deleteCategory } from "../../actions/CategoryActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

class ListCategoryComponent extends Component {
    componentDidMount() {
      console.log('category didmount');
      this.props.getCategories();
      console.log('categoryd1 didmount');
    }
    addCategory(){
      this.props.history.push('/addCategory');
    }
    onDeleteClick = id => {
      this.props.deleteCategory(id);
    };
   

    render() {
      console.log('category');
      console.log(this.props);
      const { categories } = this.props.category;
      console.log('categories');
      return (

<div className = "row">
<div >
                   
<Link to="/addCategory" className="btn btn-lg btn-outline-primary">
    Create a Category
  </Link>
  <br/>
<table className = "table table-striped table-bordered">

    <thead>
        <tr>
            <th> Category id</th>
            <th> Category Name</th>
            <th> Actions</th>
        </tr>
    </thead>
    <tbody>
    

        {
            categories.map(
                category => 
                <tr key = {category.id}>
                     <td> { category.id} </td>
                     <td> { category.name} </td>   
                     <td>
                         <button style={{marginLeft: "10px"}}  onClick={this.onDeleteClick.bind(this, category.id)} className="btn btn-danger">Delete </button>
                         <button style={{marginLeft: "10px"}}  className="btn btn-info">View </button>
                         
                         <Link to={`/updateCategory/${category.id}`}>
                  <li className="list-group-item update">
                    <i className="fa fa-edit pr-1"> update category</i>
                  </li>
                </Link>               
                     </td>
                </tr>
            )
        }
    </tbody>
</table>
</div>
</div>
      );
    }
  }
  
  ListCategoryComponent.propTypes = {
    category: PropTypes.object.isRequired,
    getCategories: PropTypes.func.isRequired,
    deleteCategory: PropTypes.func.isRequired
  };
  
  const mapStateToProps = state => ({
    category: state.category
  });
  
  export default connect(
    mapStateToProps,
    { getCategories, deleteCategory }
  )(ListCategoryComponent);