import React, { Component } from 'react'
import { getTeams, deleteTeam } from "../../actions/TeamActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

class ListTeamComponent extends Component {
    componentDidMount() {
      console.log('categoryd1');
      this.props.getTeams();
      console.log('categoryd2');
    }
    addTeam(){
      this.props.history.push('/addTeam');
    }
    onDeleteClick = id => {
      this.props.deleteTeam(id);
    };
   

    render() {
      console.log('category');
      console.log(this.props);
      const { teams } = this.props.team;
  
      return (

<div className = "row">
<div >
                   
<Link to="/addTeam" className="btn btn-lg btn-outline-primary">
    Create a Team
  </Link>
  <br/>
<table className = "table table-striped table-bordered">

    <thead>
        <tr>
            <th> Team id</th>
            <th> Team Name</th>
            <th> Actions</th>
        </tr>
    </thead>
    <tbody>
    

        {
            teams.map(
                team => 
                <tr key = {team.team_id}>
                     <td> { team.team_id} </td>
                     <td> { team.team_name} </td>   
                     <td>
                         <button style={{marginLeft: "10px"}}  onClick={this.onDeleteClick.bind(this, team.team_id)} className="btn btn-danger">Delete </button>
                         <button style={{marginLeft: "10px"}}  className="btn btn-info">View </button>
                         
                         <Link to={`/updateTeam/${team.team_id}`}>
                  <li className="list-group-item update">
                    <i className="fa fa-edit pr-1"> update team</i>
                  </li>
                </Link>               
                     </td>
                </tr>
            )
        }
    </tbody>
</table>
</div>
</div>
      );
    }
  }
  
  ListTeamComponent.propTypes = {
    team: PropTypes.object.isRequired,
    getTeams: PropTypes.func.isRequired,
    deleteTeam: PropTypes.func.isRequired
  };
  
  const mapStateToProps = state => ({
    team: state.team
  });
  
  export default connect(
    mapStateToProps,
    { getTeams, deleteTeam }
  )(ListTeamComponent);