import React, { Component } from 'react'
import { getExpenses, deleteExpense } from "../../actions/ExpenseActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Moment from 'react-moment';

class ListExpenseComponent extends Component {
    componentDidMount() {
      console.log('expense');
      this.props.getExpenses();
   
    }

    onDeleteClick = id => {
      this.props.deleteExpense(id);
    };
   

    render() {
      console.log('expense');
      console.log(this.props);
      const { expenses } = this.props.expense;
  
      return (

<div className = "row">
<div >
                   
<Link to="/addExpense" className="btn btn-lg btn-outline-primary">
    Create a Expense
  </Link>
  
<table className = "table table-striped table-bordered">

<thead>
    <tr>
        <th> Expense id</th>
        <th> Description</th>
        <th> Location</th>
        <th> Date</th>
        <th> Category</th>
        <th> Team</th>
        <th> Actions</th>
    </tr>
</thead>
    <tbody>
        {
            expenses.map(
                expense => 
                <tr key = {expense.id}>
                      <td> { expense.id} </td>
                      <td> { expense.description} </td> 
                      <td> { expense.location} </td> 
                      <td> <Moment date={expense.expensedate} format="YYYY/MM/DD"/></td> 
                      <td> {expense.category.name} </td> 
                      <td> {expense.team.team_name} </td> 
                      <td>
                         <button style={{marginLeft: "10px"}}  onClick={this.onDeleteClick.bind(this, expense.id)} className="btn btn-danger">Delete </button>
                         <button style={{marginLeft: "10px"}}  className="btn btn-info">View </button>
                         <Link to={`/updateExpense/${expense.id}`}>
                        <li className="list-group-item update">
                          <i className="fa fa-edit pr-1"> update Expense</i>
                        </li>
                </Link>               
                     </td>
                </tr>
            )
        }
    </tbody>
</table>
</div>
</div>
      );
    }
  }
  
  ListExpenseComponent.propTypes = {
    expense: PropTypes.object.isRequired,
    getExpenses: PropTypes.func.isRequired,
    deleteExpense: PropTypes.func.isRequired
  };
  
  const mapStateToProps = state => ({
    expense: state.expense
  });
  
  export default connect(
    mapStateToProps,
    { getExpenses, deleteExpense }
  )(ListExpenseComponent);